"use client"

import { useEffect, useState } from "react"
import { useParams, useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { ChevronLeft, Loader2, AlertCircle } from "lucide-react"
import { Navigation } from "@/components/navigation"

interface CardDetail {
  id: string
  name: string
  type: string
  rarity: string
  description: string
  atk?: number
  def?: number
  level?: number
  attribute?: string
  image?: string
}

export default function CardDetailPage() {
  const { id } = useParams()
  const router = useRouter()
  const [card, setCard] = useState<CardDetail | null>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchDetail = async () => {
      try {
        const res = await fetch(`/api/cards/${id}`)
        const result = await res.json()
        if (result.success) {
          setCard(result.data)
        }
      } catch (error) {
        console.error("Error fetching detail:", error)
      } finally {
        setLoading(false)
      }
    }
    
    if (id) fetchDetail()
  }, [id])

  if (loading) return (
    <div className="min-h-screen bg-background flex items-center justify-center text-white">
      <Loader2 className="w-8 h-8 animate-spin" />
    </div>
  )

  if (!card) return (
    <div className="min-h-screen bg-background flex flex-col items-center justify-center text-red-400 gap-4">
      <AlertCircle className="w-10 h-10" />
      <p>Card not found</p>
      <Button variant="outline" onClick={() => router.back()}>Go Back</Button>
    </div>
  )

  return (
    <main className="min-h-screen bg-background pb-24 pt-10 px-4">
      <div className="max-w-4xl mx-auto">
        <Button variant="ghost" onClick={() => router.back()} className="mb-6 hover:bg-secondary/20">
          <ChevronLeft className="mr-2 h-4 w-4" /> Back to Catalog
        </Button>

        <div className="grid md:grid-cols-2 gap-8 items-start">
          {/* Bagian Gambar */}
          <div className="relative aspect-[3/4] w-full max-w-sm mx-auto bg-black/40 rounded-xl overflow-hidden shadow-2xl border border-white/10">
            <img 
              src={card.image} 
              alt={card.name} 
              className="w-full h-full object-contain"
            />
          </div>

          {/* Bagian Info */}
          <div className="space-y-6">
            <div>
              <h1 className="text-3xl font-bold text-white mb-2">{card.name}</h1>
              <div className="flex flex-wrap gap-2">
                <span className="px-3 py-1 rounded-full bg-blue-600/20 text-blue-400 text-sm font-medium border border-blue-600/30">
                  {card.type}
                </span>
                <span className="px-3 py-1 rounded-full bg-purple-600/20 text-purple-400 text-sm font-medium border border-purple-600/30">
                  {card.rarity}
                </span>
                {card.attribute && (
                  <span className="px-3 py-1 rounded-full bg-yellow-600/20 text-yellow-400 text-sm font-medium border border-yellow-600/30">
                    {card.attribute}
                  </span>
                )}
              </div>
            </div>

            {/* Stats Box */}
            {(card.atk !== undefined || card.def !== undefined || card.level) && (
              <div className="grid grid-cols-3 gap-4 bg-secondary/10 p-4 rounded-lg border border-white/5">
                {card.level && (
                  <div className="text-center">
                    <p className="text-xs text-muted-foreground uppercase">Level/Rank</p>
                    <p className="text-xl font-bold text-white">â˜… {card.level}</p>
                  </div>
                )}
                {card.atk !== undefined && (
                  <div className="text-center">
                    <p className="text-xs text-muted-foreground uppercase">ATK</p>
                    <p className="text-xl font-bold text-red-400">{card.atk}</p>
                  </div>
                )}
                {card.def !== undefined && (
                  <div className="text-center">
                    <p className="text-xs text-muted-foreground uppercase">DEF</p>
                    <p className="text-xl font-bold text-blue-400">{card.def}</p>
                  </div>
                )}
              </div>
            )}

            <div className="bg-secondary/5 p-6 rounded-xl border border-white/10">
              <h3 className="text-sm font-semibold text-muted-foreground uppercase mb-3">Card Description</h3>
              <div 
                className="text-gray-300 leading-relaxed font-serif"
                dangerouslySetInnerHTML={{ __html: card.description }}
              />
            </div>
          </div>
        </div>
      </div>
      <Navigation />
    </main>
  )
}